.Main-container {
    height: 100vh;
    width: 100vw;
}

.Video-container.mobile {
    background-color: #e6e6e6;
    position: relative;
    width: 100vw;
    height: calc(100vw * (640 / 480));
    float: top;
}
  
.Video-container.desktop {
    background-color: #e6e6e6;
    position: relative;
    width: calc(100vh * (640 / 480));
    height: 100vh;
    float: left;
}

.InfoBox-container.mobile {
    position: relative;
    width: 100vw;
    height: calc(100vh - (100vw * (640 / 480)));
    float: top;
    text-align: center;
}
  
.InfoBox-container.desktop {
    position: relative;
    width: calc(100vw - (100vh * (640 / 480)));
    height: 100vh;
    float: left;
    text-align: center;
}

.checkbutton {
    font-size: 2em;
    padding: 10px;
}

#LeftSide {
    width: 50%;
    height: 98%;
    float: left;
    /*background-color: aquamarine;*/
    padding-top: 2%;
}

#RightSide {
    width: 50%;
    height: 100%;
    float: right;
    /*background-color: darkolivegreen; */
}